.container-project {
  margin-top: 200px;
  .carousel {
    min-width: 60%;

    // position: relative !important;
    // .opacity {
    //   background-color: rgba(44, 39, 39, 0.279);
    //   position: absolute;
    //   top: 0;
    //   left: 0;
    //   width: 100%;
    //   height: 100%;
    //   z-index: 1;
    // }
    .carousel-inner {
      .carousel-item {
        height: 500px !important;
        background-position: center !important;
        background-repeat: no-repeat;
        background-size: contain;
        height: "500px" !important;
        cursor: pointer;
        &:nth-child(1) {
          background-image: url("../../images/social-net.png") !important;
          transition: all ease-in 0.5s;
          &:hover {
            transition: all ease-out 0.5s;
            transform: scale(0.9);
          }
        }
        &:nth-child(2) {
          background-image: url("../../images/cosmetic-web.png") !important;
          transition: all ease-in 0.5s;
          &:hover {
            transition: all ease-out 0.5s;
            transform: scale(0.9);
          }
        }
      }
    }
  }
}

@media screen and (max-width: 768px) {
  .container-project {
    // margin-top: 0px !important;
    .containerTitleProject {
      padding: 0px !important;
      margin: 0px !important;
    }
    .carousel {
      min-width: 100%;
    }
    .containerProjectImage {
      margin-top: 19px;
    }
  }
}
